cy but also DW, the one of the DrugWars game.")]),_c('p',{attrs:{"id":"general_info"}},[_vm._v("DrugWars game has been brought to you by Futureshock which already has successful projects\n        in their history https://futureshock.world.")]),_c('h2',[_vm._v("General info")]),_c('p',[_vm._v("The game is available one telegram while there is also a discord channel\n        https://discord.gg/vwzAdva where regular news\n        with updates are published. The basic gameplay\n        consists of building an empire through upgrades of numerous buildings, collecting exclusive NFTs, and\n        battling against other players.")]),_c('h2',[_vm._v("Login and signup")]),_c('p',[_vm._v("To be able to play the game, a person needs a telegram account and a TON blockchain wallet. Logging\n        in to the game is completely free of charge. To log in, visit https://drugwars.io/ and click on Play.")]),_c('p',[_vm._v("Once a player is logged in, he or she can continue to personalize his account and settings by choosing a\n        nickname, profile picture, and alert options. There is also a referral link and different tasks available\n        which can be sent to\n        people so the player receives an additional bonus.")]),_c('hr'),_c('h2',[_vm._v("User Settings")]),_c('p',[_vm._v("Settings are located in the bottom Home page on the DrugWars platform.")]),_c('h3',[_vm._v("Settings")]),_c('p',[_vm._v("Players can change their profile picture no more than once a month and they can not do it for two days after\n        their\n        last started battle with other players. Make sure that you use the image that is not protected by any\n        copyright laws. DrugWars\n        will not be responsible for any violations of copyrights done by its players.")]),_c('hr'),_c('h3',[_vm._v("Referral")]),_c('p',[_vm._v("Share your referral link with your friends and receive a bonus, all you need to do is copy it and send\n        it.\n        When your friend clicks on that referral link, they will go through the login process as usual. Once they\n        finish\n        it, you will see their info under “YOUR REFERRALS”. There is no limit to how many players you\n        can\n        refer to the game.")]),_c('h1',[_vm._v("DrugWars platform appearance")]),_c('p',[_vm._v("DrugWars platform is available at https://drugwars.io.")]),_c('h2',[_vm._v("Production Banner")]),_c('p',[_vm._v("At the very top of the DrugWars platform, there is a production banner that has information about your\n        current production of resources. You can see how many drugs/weapons/alcohol you have with information about\n        how much you produce daily. You are also\n        informed about your daily DW rewards in the line beneath your production info. Your prize is\n        calculated based on the overall drug production in the game which is also presented in that text line.")]),_c('hr'),_c('h3',[_vm._v("Home")]),_c('p',[_vm._v("The Overview is the landing page that you will see when you log in to the game. It consists of two parts;\n        the\n        Overview and Tutorial. It is located at the first place of the left menu.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image10.jpg"}})]),_c('h4',[_vm._v("2.2.1.1 Overview")]),_c('p',[_vm._v("The overview will show you your production of resources in 3 categories: Drugs, Weapons, and Alcohol\n        production. Each resource has 5 buildings that contribute to how much each resource generates per day and\n        per\n        hour. Under sections is the information about total resources that you produce per day and per hour and the\n        time remaining to have full capacity of resources based on your production. You will also see your total\n        capacity and how much of that is safe which is controlled by Drug Storage, Weapon Storage, and Alcohol Silo.\n      ")]),_c('h4',[_vm._v("2.2.1.2 Tutorial")]),_c('p',[_vm._v("For new players, the tutorial is the best place to start and get familiar with the game. You will see a\n        welcome message from our developer and CEO. We have prepared several simple tasks you will need to complete.\n        They are specifically designed so you get accustomed to where things are and how to play. When you click on\n        each task, it will guide you in how to complete it.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image93.jpg"}})]),_c('h3',[_vm._v("2.2.2 Buildings")]),_c('p',[_vm._v("Everything in DrugWars is in some way connected to buildings. A player produces resources with them, they\n        allow the purchase and training of battle units, keep your resources safe, etc. There are five types of\n        buildings, Office, Drugs, Weapons, and Alcohol buildings can be reached in the left menu by clicking on\n        Buildings, while Gang Buildings are available through the Gang tab in the left menu. To learn about what\n        each\n        individual building produces and how it functions read Production.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image117.jpg"}})]),_c('p',[_vm._v("Each building has information about the required resources for its upgrade just below its title,\n        description,\n        current production and the production that will happen with the next upgrade, the time required for upgrade,\n        and two upgrade links: normal (top one in green color) and instant one bottom one in yellow color).")]),_c('p',[_vm._v("If you have all the resources necessary, they will be in white color while the UPGRADE button will be\n        green.\n        If you are missing resources, they will be in red color while the UPGRADE button will say “MISS\n        RESOURCES” and be black.")]),_c('p',[_vm._v("Every upgrade requires a certain amount of time to be completed which is different for each building. Once\n        you click on the UPGRADE button, your resources will be lowered by the amount needed for the upgrade\n        (spent),\n        the upgrade will start and your building will rise in its level, by one, when the set time has passed. If\n        you\n        want an instant upgrade, click on the yellow button that says how much crypto you will spend on it. The\n        instant upgrade will raise your building level immediately and instead of spending your resources, you will\n        pay for it with crypto. If you do not have enough crypto for the upgrade, this option will not be available\n        and the button will be black instead of yellow.")]),_c('h4',[_vm._v("2.2.2.1 Office")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image91.jpg"}})]),_c('p',[_vm._v("Office buildings do not create resources but they are the ones that make it possible for other buildings to\n        create them. In that way, they are the most important ones because they impact other buildings and condition\n        if you can upgrade them or not. To learn about each office building and how it functions, read about it in\n        Office Production.")]),_c('h4',[_vm._v("2.2.2.2 Drugs")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image50.jpg"}})]),_c('p',[_vm._v("Drug buildings produce the drug resource, there are five of them and if you want to learn about each drug\n        building, read about it in Drugs.")]),_c('h4',[_vm._v("2.2.2.3 Weapons")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image123.jpg"}})]),_c('p',[_vm._v("Weapon buildings produce the weapon resource, there are five of them and if you want to learn about each\n        weapon building, read about it in Weapons.")]),_c('h4',[_vm._v("2.2.2.4 Alcohol")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image74.jpg"}})]),_c('p',[_vm._v("Alcohol buildings produce the alcohol resource, there are five of them and if you want to learn about each\n        alcohol building, read about it in Alcohol.")]),_c('h4',[_vm._v("2.2.2.5 Defense")]),_c('p',[_vm._v("There are six defensive buildings providing different aspects of defense around your base. If you want to\n        learn about each alcohol building, read about it in Defense.")]),_c('h3',[_