ensive -> Attack: The strength of attack.")]),_c('li',[_vm._v("Offensive -> Carry: The amount of combined resources the unit can carry.")]),_c('li',[_vm._v("Defensive -> Health: The hit points a unit can take before being killed.")]),_c('li',[_vm._v("Defensive -> Resistance: This is the “shielding” value for the unit.")]),_c('li',[_vm._v("Extra -> Speed: The amount of time it takes to transport the unit. Transporting 1 unit takes this\n          amount of time and transporting 100 of the same unit also takes this amount of time. Increasing or\n          decreasing the number of the unit does not change the time requirement.")]),_c('li',[_vm._v("Extra -> Move Cost: This is the cost, in Drugs, to move each unit. Moving one unit will cost the\n          stated\n          value. Moving 100 unites will cost the value * 100.")])]),_c('p',[_vm._v("The name of each unit is followed by a category, either Melee or Range. Malee units are more the\n        hand-to-hand\n        combat units while the Range units attack from a distance.")]),_c('h3',[_vm._v("2.2.4 Actions")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image72.jpg"}})]),_c('p',[_vm._v("Actions is the place a player needs to go if he wants to attack other players, occupy a new base or\n        transport\n        resources and units to his bases. There are 4 additional tabs within actions, the first, attack, is\n        automatically opened. That one is the only responsive one that allows players different actions while tabs\n        out, in, and station are informative. They provide information about what is going on.")]),_c('h4',[_vm._v("2.2.4.1 Actions")]),_c('p',[_vm._v("There are 4 actions a player can take. He can ATTACK one of his opponents, TRANSPORT resources to another\n        base, OCCUPY a new base or STATION units to another base.")]),_c('h2',[_vm._v("2.2.4.1.1 Attack")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image18.jpg"}})]),_c('p',[_vm._v("To attack someone, first select your army. It is done by typing in the number in the blank space below the\n        image, then clicking on the image to add. To start a fight, target coordinates must be entered. A fight\n        message is optional and should not be insulting or discriminating regarding gender, sexual and religious\n        preferences, nationality, skin color, health etc. or it will be sanctioned by the developers and punished\n        accordingly.")]),_c('h2',[_vm._v("2.2.4.1.2 Transport")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image89.jpg"}})]),_c('p',[_vm._v("A player can both transport resources to one of his own bases or to another player. To be able to transport\n        resources, there must be units selected who will carry those resources. After selecting units and resources,\n        a\n        player must enter the coordinates of the base where those resources must go and click on TRANSPORT. To learn\n        more about sharing resources with other players, read Sharing resources with other players.")]),_c('h2',[_vm._v("2.2.4.1.3 Occupy")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image59.jpg"}})]),_c('p',[_vm._v("To occupy a new base, a player must first have 1 occupation troop. It is not possible to occupy a new\n        territory without this unit because the unit itself transforms into the base. Once the unit is selected, a\n        player needs to enter the coordinates of the territory and choose a name for the new base. After the button\n        OCCUPY is clicked on, new base will be created instantly. To learn more about occupying new bases, read\n        Choosing secondary base locations.")]),_c('h2',[_vm._v("2.2.4.1.4 Station")]),_c('p',[_vm._v("To station units at another base, follow the same procedure as for the transport of resources. The only\n        difference here is that you wont need to select resources, you will only need to select the units you wish\n        to\n        station at another base.")]),_c('h4',[_vm._v("2.2.4.2 Out")]),_c('p',[_vm._v("The out tab will show you information about outgoing actions, those that a player has started himself.\n        Those\n        actions will be listed with general information who won and lost but you can get more information if you\n        simply click on the SHOW DETAILS. Since outgoing actions (just like incoming) have a time delay, there will\n        be\n        a number in the brackets that will indicate how many battles will happen. When a player clicks on OUT that\n        has\n        a number in the brackets, battles that will happen will be listed before those that already did with the\n        exact\n        time when the fight will happen.")]),_c('h4',[_vm._v("2.2.4.3 In")]),_c('p',[_vm._v("The in tab will show you information about incoming actions, those that a player did not start but were the\n        actions invoked on him by other players. Those battles will be listed with general information who won and\n        lost but you can get more information if you simply click on the SHOW DETAILS. Since incoming actions (just\n        like outgoing) have a time delay, there will be a number in the brackets that will indicate how many battles\n        will happen. When a player clicks on IN that has a number in the brackets, actions that will happen will be\n        listed before those that already did with the exact time when the fight will happen.")]),_c('h4',[_vm._v("2.2.4.5 Station")]),_c('p',[_vm._v("Station tab is informative just like Out and In tabs. This one will provide information about units that\n        are\n        being stationed at a certain base. Station is a time delayed action and there will be a number in the\n        brackets\n        indicating how a stationing will happen. Station activities can be seen in Actions -> Station, there will\n        be information on units and the time when they will arrive at the base. In the example below, there are no\n        ingoing or outgoing actions but there is one station action that will happen which is indicated by the\n        numbers\n        in the brackets.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image100.jpg"}})]),_c('h3',[_vm._v("2.2.5 Map")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image110.jpg"}})]),_c('p',[_vm._v("Map is the place where a player chooses his main location and several additional ones. It is also the place\n        where he can choose which location to attack and start a battle with a player that is on it. To choose any\n        location either for his base or for attack, a simple click on the hexagon is needed. The Earth is\n        interactive\n        and is slowly spinning around its axis but a player can turn it around manually by clicking and moving his\n        mouse. To learn about how you can choose your bases, read Player location on the map.")]),_c('h3',[_vm._v("2.2.6 Leaderboards")]),_c('p',[_vm._v("Leaderboards is where you can see the list of top players in 6 categories of game play, Production,\n        Prestige,\n        Heist, Fight, Job and Lottery. Each list is generated based on the results of every player in the game. Hall\n        of fame lists the top users in battles, Production lists the top players in production of the drug resource,\n        and the Heist lists those who have won the most in the daily heist. There are bonus awards in crypto for\n        players who are on the lists that can be seen next to the names of players.")]),_c('h4',[_vm._v("2.2.6.1 Production (PROD)"),_c('sup',[_vm._v("[a]")])]),_c('p',[_c('img',{attrs:{"src":"/img/help/image133.jpg"}})]),_c('p',[_vm._v("Production list has the names of the best players based on their Drug production. Production of weapons and\n        alcohol are listed too but the list is generated solely on how much drugs a player produces. As long as a\n        player is on the list, he or she will receive an additional prize in crypto that is listed right from the\n        production info of each player. This reward is not the same as the daily reward for production, to read\n        about\n        the daily production reward, click here: Daily prize.")]),_c('h4',[_vm._v("2.2.6.2 Cruelty")]),_c('h4',[_vm._v("2.2.6.3 Heist")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image129.jpg"}})]),_c('p',[_vm._v("Heist list has the names of players who have deposited the biggest drug amounts into heist. As long as the\n        player is on the list, he or she will receive additional reward in crypto that is listed right from the\n        amount\n        of drugs deposited. This reward is not the same as the daily heist reward, to read about a daily heist\n        reward,\n        click here: Heist prize. To learn how you can invest your drugs into heists, click here: Heist.")]),_c('h4',[_vm._v("2.2.6.4 Fight")]),_c('h4',[_vm._v("2.2.6.5 Job")]),_c('h4',[_vm._v("2.2.6.6 Lottery")]),_c('h3',[_vm._v("2.2.7 Gangs")]),_c('p',[_vm._v("When you are in the Gang tab, all the gangs are listed in the main frame so you can scroll through them and\n        read their descriptions. If a player chooses, he can create or join an existing gang. To learn all about\n        gangs, read about it here: GANGS. When you click on Gangs in the left menu, there are two possible main\n        frames\n        that may appear. If you are not yet a part of any gang, you will see the following upper menu above the main\n        frame:")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image60.jpg"}})]),_c('h4',[_vm._v("2.2.7.1 Joining a Gang")]),_c('p',[_vm._v("You can apply to join a gang by simply clicking on their name in the gang list. Once done, the application\n        page of that gang with their info and the input space for you to write a message to them. That writing is\n        optional but highly recommended. When you are done, click on the apply button.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image130.jpg"}})]),_c('h4',[_vm._v("2.2.7.2 Create a Gang")]),_c('p',[_vm._v("If you want to create your own gang, click on the Create Gang tab. It will open the following in the main\n        screen if you do not have enough resources:")]),_c('p',[_vm._v("If you do have the necessary resources, all you need to do is add a name and description before you click\n        on\n        CREATE and your new gang will be ready.")]),_c('h4',[_vm._v("2.2.7.3 Being a Gang Member")]),_c('p',[_vm._v("Once you become a gang member, every time you click on Gangs in the left menu, the main frame will have the\n        following upper menu:")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image75.png"}})]),_c('p',[_vm._v("By clicking on various parts in that menu, you will be diverted to different gang related material. Only\n        the\n        Gangs page will look the same as it would if you were not in the gang. My gang will list you all the gang\n        members, Buildings will show you gang buildings and options to donate your resources to them, Diplomacy will\n        get you options to declare wars or alliances and in Deposit, you can see which gang member has donated how\n        much to each gang building. To get information about how gang buildings function and what are their\n        benefits,\n        click here: Gang buildings")]),_c('h4',[_vm._v("2.2.7.4 Gang Roles")]),_c('p',[_vm._v("Within a Gang, there are roles of which you are assigned. The lead is the Boss. After the Boss, there is\n        Capo. A Capo is assigned by the Boss. Capo has a none-to-many relationship to the Gang, meaning there can be\n        no Capo, or many Capo, at the discretion of the Boss.")]),_c('h3',[_vm._v("2.2.8 Jobs")]),_c('p',[_vm._v("There are two types of jobs, “JOBS” and “GANG JOBS”. Both Jobs and Gang Jobs are\n        started by clicking the START JOB button.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image82.png"}}),_vm._v(" "),_c('img',{attrs:{"src":"/img/help/image115.png"}})]),_c('h4',[_vm._v("2.2.8.1 (Solo) Jobs"),_c('img',{attrs:{"src":"/img/help/image15.png"}}),_c('img',{attrs:{"src":"/img/help/image112.png"}})]),_c('p',[_vm._v("Jobs are solo jobs that are done by the individual player alone. Once the job is started, you have until\n        the\n        timer completes to finish the job. Finishing the job simply means that all the DRUGWARS NPC units need to be\n        killed. Once complete, you receive the job rewards as stated.")]),_c('h4',[_vm._v("2.2.8.2 Gang Jobs")]),_c('p',[_vm._v("Gang Jobs are similar to regular Jobs, except they apply to the whole gang. One to many gang members may\n        participate in a gang job and the rewards are shared amongst the active players for that day, plus an\n        additional Gang Bonus.")]),_c('h3',[_vm._v("2.2.9 Market")]),_c('p',[_vm._v("Market is the place where you can safely purchase or withdraw your DWD tokens. For purchasing, please\n        consider that we always recommend you to use SteemEngine to get DWD tokens at a fair price decided by\n        players.")]),_c('h4',[_vm._v("2.2.9.1 Market")]),_c('h4',[_vm._v("2.2.9.2 Withdraw")]),_c('h3',[_vm._v("2.2.10 Settings")]),_c('p',[_vm._v("Settings are explained in the introduction part of this document. See User Settings.")]),_c('h3',[_vm._v("2.2.11 Help")]),_c('p',[_vm._v("The basic information you need to know about the game is located in the bottom left menu behind the button\n        Help. It has 3 parts, Help (FAQ), Get Started, and Guides. We suggest to all of our players to read this\n        part\n        once they start playing the game.")]),_c('h4',[_vm._v("2.2.11.1 Help"),_c('img',{attrs:{"src":"/img/help/image23.jpg"}})]),_c('p',[_vm._v("The help part Frequently Asked Questions has appropriate answers below each question. You can read about\n        gameplay, errors you may receive if you do not have enough resources for upgrades, awards, battles, player\n        info etc. The extended FAQ is available at the end of this document, to skip to it, click here: F.A.Q.")]),_c('h4',[_vm._v("2.2.11.2 Get started")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image63.jpg"}})]),_c('p',[_vm._v("The Get started has basic and general advice about what you should do once you start playing the game. It\n        will tell you to produce resources, build units, and attack other players. It will also wish you luck and\n        remind you that this is a game and all is fair. DrugWars is not responsible for any psychological problems\n        or\n        addictions. If you notice that playing the game is keeping you from your normal life and activities, please\n        seek professional help.")]),_c('h4',[_vm._v("2.2.11.3 Guides")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image27.jpg"}})]),_c('p',[_vm._v("Guides will regularly be updated with links to guides which were created by the community of DrugWars for\n        the\n        community of DrugWars. If you have built a guide that you think can help other players, feel free to contact\n        us and send us the link. We will see if your work is of quality and beneficial to other players.")]),_c('h3',[_vm._v("2.2.12 Forum")]),_c('p',[_vm._v("DrugWars has its official forum that can be reached either by clicking on the Forum in the bottom left menu\n        or by visiting the link: https://drugwars.tokenbb.io/ (the page that the button in the menu will open\n        in\n        a new tab once you click on it). The forum needs your login that can be done by your Steem account\n        credentials, SteemConnect, Google or Facebook account or with your e-mail.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image119.jpg"}})]),_c('p',[_vm._v("Forum is a place where you can read all about what is new with the game and share your experience with\n        other players. It is also the place where you can share your battles. To learn more about sharing battles on\n        Forum, click here: Sharing Battles.")]),_c('h3',[_vm._v("2.2.13 Logout")]),_c('p',[_vm._v("The last thing in the left menu area is the logout button. When you click on it, the game will log you out\n        all the way but your progress with the game will not be lost. All of your production will continue and you\n        will be visible to other players for battles. You can be logged out and still under attack and participating\n        in battles because logging out does not stop your game progress in any way.")]),_c('h3',[_vm._v("2.2.12 Links area"),_c('img',{attrs:{"src":"/img/help/image31.jpg"}})]),_c('p',[_vm._v("There are several links in the link area. You can vote for Futureshock (creators of DrugWars) as a steem\n        witness, control the game sound, learn about the game and visit DrugWars on Discord and Github. Links to\n        sites\n        outside DrugWars will be opened in new tabs and your game will not be disturbed while those connected with\n        the\n        game will open in the main frame of the game.")]),_c('h4',[_vm._v("2.2.12.1 Future Witness")]),_c('p',[_vm._v("Steem blockchain has witnesses. They are people who operate a witness server where blocks are being\n        produced.\n        Witnesses produce blocks, maintain a price feed, play the role of consensus, and work on different things to\n        help Steem function, grow and evolve. Witnesses get voted into their positions by Steem users and each steem\n        user gets 30 votes. You can check the witness list and vote here: https://steemit.com/~witnesses.\n        FutureShock,\n        as the creator of DrugWars, is also a witness on steem blockchain. If you click on the provided\n        link Vote\n        for @future.witness and support DrugWars it will direct you to SteemConnect where you can easily cast\n        your vote by following a simple procedure. DrugWars team would appreciate it if you give us your vote.\n      ")]),_c('h4',[_vm._v("2.2.12.2 Sound control")]),_c('p',[_vm._v("By clicking on the sound icon "),_c('img',{attrs:{"src":"/img/help/image132.jpg"}}),_vm._v(" you control whether or not you want\n        to experience the DrugWars game with a little music being played in the background. You can change that\n        preference whenever you want. For your alerts of the upcoming attacks to work, the sound feature must be\n        turned on.")]),_c('h4',[_vm._v("2.2.12.3 About")]),_c('p',[_vm._v("The question mark link "),_c('img',{attrs:{"src":"/img/help/image88.jpg"}}),_vm._v("will open the about content in the main frame.\n        Here\n        you can read the basic info, download the logo or report a bug. It also shows the changelog info and the\n        DrugWars catchphrase and motto: “The drugs are virtual but the money is real”.")]),_c('h4',[_vm._v("2.2.12.4 Discord link")]),_c('p',[_vm._v("Discord is a proprietary freeware VoIP application that allows its users to communicate\n        through text, image, video, and audio. DrugWars has its Discord server with many different rooms. It is\n        designed in a way that allows our players to communicate with each other and get regular and relevant\n        information about the game. It is free and simple to use with the moderation of language done by DrugWars\n        officers so that communication does not fall into the uncivilized gutter. You can join DrugWars discord by\n        clicking on the link "),_c('img',{attrs:{"src":"/img/help/image11.jpg"}}),_vm._v(" that is present on the platform and directs to\n        https://discord.gg/vwzAdva.")]),_c('h4',[_vm._v("2.2.12.5 Github link")]),_c('p',[_vm._v("DrugWars game is completely open sourced and transparent which means that any player is free to check out\n        the\n        code behind the game and follow through how the game is being developed. If you click on the last link "),_c('img',{attrs:{"src":"/img/help/image124.jpg"}}),_vm._v(" it will open the Github page of Futureshock\n        https://github.com/FutureShockco in a new tab.")]),_c('h2',[_vm._v("2.3 Right menu")]),_c('p',[_vm._v("DrugWars is a game that financially rewards its players in cryptocurrency. The amount that is awarded each\n        day is presented in the right menu which is composed of the Daily prizes info in 3 parts and the Heist.\n        Prizes\n        are independent of each other so each player can get a reward in all 4 categories or in none at all\n        depending\n        on his or her achievements in the game. The prizes are given every 24 hours and directly sent to players'\n        DrugWars account.")]),_c('h3',[_vm._v("2.3.1 Daily prizes info")]),_c('p',[_vm._v("Today's prize is the overall amount that is given to all players. It is divided into 3 parts, Battle,\n        Daily,\n        and Heist. Percentages show how much of the overall amount is attributed to each of those 3 categories of\n        prizes. The information is highlighted yellow and located under the overall amount."),_c('img',{attrs:{"src":"/img/help/image69.jpg"}})]),_c('p',[_vm._v("There is a countdown clock at the top of the right menu which is counting down the time until the next\n        payment while the current awards which a player might get are under his banner production in a single\n        sentence\n        line. That info can change during the day depending on the actions of the player.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image24.jpg"}})]),_c('h4',[_vm._v("2.3.1.1 Daily Prize")]),_c('p',[_vm._v("Daily prize is the award a player gets based on drug production. It is calculated based on the percentage\n        of\n        drugs produced out of the total drug production of all players. Those who produce more drugs get a higher\n        prize than those who have a smaller production and the prize can change with each day. If a player starts\n        producing more drugs, this award will get bigger. It can also become smaller if the player stays on its\n        current drug production and other players rise in theirs.")]),_c('h4',[_vm._v("2.3.1.2 Fight Prize")]),_c('p',[_vm._v("Fight prize is the prize a player gets based on how much he or she has reduced the population. In DrugWars,\n        each unit has a supply characteristic. Population of DrugWars is calculated by multiplying a unit with its\n        supply and adding up all those together from all units of all players. By participating in battles, a\n        winning\n        player kills units from other players, those units multiplied by their supply are the amount by which the\n        overall population has been reduced. Since different units have different supplies, it is not the same to\n        kill\n        100 Hobos (supply = 100*1 = 100) and 100 Ninjas (supply = 100*5 = 500) for example. At the end of each day,\n        results from all the battles a player has won get added together and based on the total reduction of\n        DrugWars\n        population, a player gets rewarded. The info about current DrugWars population is below the prizes info in\n        the\n        right menu,")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image99.jpg"}})]),_c('h4',[_vm._v("2.3.1.3 Heist Prize")]),_c('p',[_vm._v("Heist prize is the award a player gets by participating in the heist. Each day, every player can decide to\n        invest his or her drug in the heist and get rewarded by it. Players who invest more get bigger rewards from\n        those who invest less and since the prize is dependent on the investment of others, it may change during the\n        day when others invest more than the player.")]),_c('h3',[_vm._v("2.3.2 Heist"),_c('img',{attrs:{"src":"/img/help/image1.jpg"}})]),_c('p',[_vm._v("Every player has an option to invest drugs in the heist every day. It is done simply by entering the amount\n        in the white space and clicking on Deposit. If a player wants to invest all drugs, there is a button Deposit\n        all for that. A player can deposit drugs as many times as he wants during the day but those drugs that were\n        invested cannot be gotten back.")]),_c('h2',[_vm._v("2.4. DrugWars Live Chat")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image68.jpg"}})]),_c('p',[_vm._v("Live chat is located in the bottom right corner and it is collapsed by default. Clicking on the small arrow\n        will expand it into normal size. The chat is a place where players can talk to each other in real time by\n        entering messages and clicking enter to send them to others. There are options of private chat, group, and\n        gang.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image98.jpg"}})]),_c('h1',[_vm._v("3. Gameplay")]),_c('p',[_vm._v("There are 3 main components of gameplay in DrugWars. As a player, you can produce, battle against other\n        players, and participate in heists. Each of those components is a complex time managed part of the game with\n        various parts to take into consideration. The components are also intertwined and there is no one without\n        the\n        other. You can not battle unless you produce units just as you can not participate in the heist if you do\n        not\n        have any drug resources produced.")]),_c('h2',[_vm._v("3.1 Production")]),_c('p',[_vm._v("Every player can produce two categories of things in the game: RESOURCES and UNITS. Resources are produced\n        by\n        buildings and used to upgrade those buildings or to produce (buy and train) units for battles. Both\n        resources\n        and units can be produced in a time managed way or instantly if a player decides to pay for faster\n        production.\n      ")]),_c('h3',[_vm._v("3.1.1 Resources")]),_c('p',[_vm._v("There are 3 types of resources in the game, drugs, weapons, and alcohol. Each resource is produced by its\n        corresponding buildings. Each building has different amounts of resources needed for its production that\n        rise\n        with each level. When a player starts playing a game, he or she already has a small amount of each resource\n        to\n        start with. A player CAN NOT have infinite amounts of resources. The amounts of maximum resources are\n        defined\n        with storage buildings for every resource. The maximum level of any building is defined with the current\n        level\n        of headquarters. A building can not be upgraded to a level higher than the headquarters are currently. If\n        headquarters are, for example, on level 5, all other buildings can go to level 5 and no higher until the HQ\n        gets upgraded.")]),_c('p',[_vm._v("TIP: While there are many ways to approach strategy in DrugWars, a good starting point is a ratio of 1:2:2,\n        meaning 1 DRUG to 2 WEAPON to 2 ALCOHOL. So if you are producing 100k drugs, you want to be around 200k each\n        of both weapons and alcohol.")]),_c('h4',[_vm._v("3.1.1.1 Drugs")]),_c('p',[_vm._v("Drugs are the most important resource in the DrugWars game. Players receive their daily prize based on the\n        amount of drugs they produce but they can get drugs by either producing or by stealing them from other\n        players\n        in battles. To ensure that their drugs do not get stolen, players can upgrade their Drug Storage building\n        and\n        keep a portion of their drug resource unavailable to steal. Each building has the following information:\n        level\n        of the building that is over the image, name, cost for the next upgrade in resource under the name and in\n        crypto for instant upgrade, description, and the amount of production on current and next level per day.\n        Each\n        building associated with drugs requires all 3 resources for upgrades; drugs, weapons, and alcohol.")]),_c('h2',[_vm._v("3.1.1.1.1 Crack House")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image134.jpg"}})]),_c('p',[_vm._v("Crack House is the basic drug production building that is very cheap to upgrade but also produces the least\n        drugs in comparison with other buildings. This building is a good thing to start with when you are first\n        starting to play the game so your production increases in time and you can afford to upgrade more useful\n        ones.\n        In the picture above is the example of how on current level 18 a player produces 7.34 thousand drugs every\n        day\n        with this building while he will produce 7.81 thousand drugs on level 19. Since the resources are in white,\n        it\n        indicates he has enough of them. If he clicks on the “upgrade” button he would have to wait for\n        41\n        minutes and 28 seconds for the upgrade to finish and for the building to rise in level.")]),_c('h2',[_vm._v("3.1.1.1.2 Hydroponic Farm")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image84.jpg"}})]),_c('p',[_vm._v("This building is next in line when it comes to producing drugs. It is still in the category of fairly cheap\n        buildings when it comes to producing drugs. In the picture above is the example of how on current level 6 a\n        player produces 8.24 thousand drugs per day with this building while he would produce 9.71 thousand on level\n        7. Since the resources are in white, it indicates that he has enough of them. If he clicks on the\n        “upgrade” button he will have to wait for 41 minutes and 28 seconds for the upgrade to finish\n        and\n        for the level of the building to rise.")]),_c('h2',[_vm._v("3.1.1.1.3 Meth Lab")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image128.jpg"}})]),_c('p',[_vm._v("The Meth Lab has a medium production of drugs and first to consider if you want to produce drugs seriously.\n        In the example above, a player has production of 18.14 thousand drugs with this building per day while he\n        will\n        have that one of 21.98 when he upgrades the building to level 6. Since the resources are in white, it\n        indicates he has enough of them.")]),_c('h2',[_vm._v("3.1.1.1.4 Cocina")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image46.jpg"}})]),_c('p',[_vm._v("Cocina is the second drug production building with medium production. Costs of her upgrade are much higher\n        than of other buildings but so is her production. In the example above, a player produces 54.43 thousands\n        drugs with it per day while he would produce 65.94 thousand when he upgrades it to the next level. Cost for\n        upgrades in white indicates that a player has enough of them while the red amount of weapons and alcohol\n        shows\n        that player does not have enough. Lack of necessary resources has disabled the upgrade button which now has\n        “miss resources” instead but instant upgrades with crypto are still available.")]),_c('h2',[_vm._v("3.1.1.1.5 Pharmaceutical Lab")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image126.jpg"}})]),_c('p',[_vm._v("Pharmaceutical lab is the most expensive drug production building but it is also the one which has the\n        highest production. It massively produces drugs and you will not go wrong if you focus on upgrading it\n        regularly to ensure your dominance in the game. In the example above, a player produces 153.86 thousands of\n        drugs with this building per day at level 6 while he would produce 181.2 thousand on the next, seventh\n        level.\n        All his resources needed for the upgrade are in the red which indicates he is lacking them and can only be\n        upgraded by paying with crypto.")]),_c('h2',[_vm._v("3.1.1.1.6 Drug Storage")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image41.jpg"}})]),_c('p',[_vm._v("Drug storage is the only drug building that does not produce drugs. This building has two functions, it\n        stores drugs and it keeps them safe. A player CANNOT have an infinite amount of drugs. The maximum amount of\n        drugs that a player may hold is defined with drug storage and by upgrading its level, the storage increases.\n        There is a possibility that some other building requires more drugs than a player can have unless he\n        upgrades\n        the storage enough. For example, a building needs 500K drugs for the upgrade but the maximum that a player\n        can\n        have is 450K. He will not be able to collect that amount of drugs if he does not upgrade his drug storage\n        accordingly. All drug production that is above the storage capacity is NOT saved anywhere and is going to\n        waste to upgrade your storage to make sure you are not producing drugs for nothing.")]),_c('p',[_vm._v("The second function this building has is the safe. A certain amount of drugs are being kept safe in the\n        building and can not be stolen in battles when a player loses. With each level, the amount that is safe from\n        stealing increases so a player even when losing a battle will not lose all of his resources.")]),_c('p',[_vm._v("Drug Storage is limited to the level of the Headquarters. If the Headquarters is level 10, Drug Storage can\n        be up to level 10.")]),_c('h4',[_vm._v("3.1.1.2 Weapons")]),_c('p',[_vm._v("Weapons are the second resource in DrugWars. There are needed for upgrades of buildings and purchases of\n        units. This resource (just as any other) can get stolen if a player loses a battle but he can keep a portion\n        of it safe by upgrading his weapon storage. Each building has the following information: level of the\n        building\n        that is over the image, name, cost for the next upgrade in resource under the name and in crypto for instant\n        upgrade, description, and the amount of production on current and next level per day. Each building\n        associated\n        with weapons requires resources of drugs and alcohols for its upgrade while the weapon storage needs all 3\n        resources.")]),_c('h2',[_vm._v("3.1.1.2.1 Ammunition Factory")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image78.jpg"}})]),_c('p',[_vm._v("Ammunition factory is the basic weapon production building that is very cheap to upgrade but also produces\n        the least weapons in comparison with other buildings. This building is a good thing to start with when you\n        are\n        first starting to play the game so your production increases in time and you can afford to upgrade more\n        useful\n        ones. In the picture above is the example of how on current level 18 a player produces 14.68 thousand\n        weapons\n        every day with this building while he will produce 15.63 thousand weapons on level 19. Since the resources\n        are\n        in white, it indicates he has enough of them. If he clicks on the “upgrade” button he would have\n        to wait for 46 minutes and 38 seconds for the upgrade to finish and for the building to rise in level.")]),_c('h2',[_vm._v("3.1.1.2.2 Weapon Factory")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image127.jpg"}})]),_c('p',[_vm._v("This building is next in line when it comes to producing weapons. It is still in the category of fairly\n        cheap\n        buildings when it comes to producing weapons. In the picture above is the example of how on current level 11\n        a\n        player produces 25.32 thousand weapons per day with this building while he would produce 27.87 thousand on\n        level 12. Since the resources are in white, it indicates that he has enough of them. If he clicks on\n        “upgrade” button he will have to wait for 55 minutes and 5 seconds for the upgrade to finish and\n        for the level of the building to rise.")]),_c('h2',[_vm._v("3.1.1.2.3 Smuggle")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image85.jpg"}})]),_c('p',[_vm._v("Smuggle is the building with medium production of weapons and first to consider if you want to produce\n        weapons seriously. In the example above, a player has production of 50.86 thousand weapons with this\n        building\n        per day while he will have that one of 57.02 when he upgrades the building to level 10. Since the resources\n        are not all in white, it indicates he hasn't got enough of them. The only way to upgrade the building is by\n        buying an instant upgrade with crypto or by waiting until he has enough resources for a time managed\n        upgrade.\n      ")]),_c('h2',[_vm._v("3.1.1.2.4 Black Market")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image86.jpg"}})]),_c('p',[_vm._v("Black Market is the second weapon building with medium production. Costs of her upgrade are much higher\n        than\n        of other buildings but so is her production. In the example above, a player produces 97.07 thousands weapons\n        with it per day while he would produce 111.97 thousand when he upgrades it to the next level. Cost for\n        upgrades in red indicates that a player hasn't got enough of them. Lack of necessary resources has disabled\n        the upgrade button which now has “miss resources” instead but instant upgrades with crypto are\n        still available.")]),_c('h2',[_vm._v("3.1.1.2.5 3D Metal Printing")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image51.jpg"}})]),_c('p',[_vm._v("3D metal printing is the most expensive weapon production building but it is also the one which has the\n        highest production. It massively produces weapons and you will not go wrong if you focus on upgrading it\n        regularly to ensure your dominance in the game. In the example above, a player produces 181.44 thousands of\n        weapons with this building per day at level 5 while he would produce 219.8 thousand on the next, sixth\n        level.\n        All his resources needed for the upgrade are in the red which indicates he is lacking them and can only be\n        upgraded by paying with crypto.")]),_c('h2',[_vm._v("3.1.1.2.6 Weapon Storage")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image43.jpg"}})]),_c('p',[_vm._v("Weapon storage is the only weapon building that does not produce weapons. This building has two functions,\n        it\n        stores weapons and it keeps them safe. A player CAN NOT have an infinite amount of weapons. The maximum\n        amount\n        of weapons that a player may hold is defined with weapons storage and by upgrading its level, the storage\n        increases. There is a possibility that some other building requires more weapons than a player can have\n        unless\n        he upgrades the storage enough. For example, a building needs 500K weapons for the upgrade but the maximum\n        that a player can have is 450K. He will not be able to collect that amount of weapons if he does not upgrade\n        his weapons storage accordingly. All weapons production that is above the storage capacity is NOT saved\n        anywhere and is going to waste to upgrade your storage to make sure you are not producing weapons for\n        nothing.\n      ")]),_c('p',[_vm._v("The second function this building has is the safe. A certain amount of weapons are being kept safe in the\n        building and can not be stolen in battles when a player loses. With each level, the amount that is safe from\n        stealing increases so a player even when losing a battle will not lose all of his resources. The example\n        above\n        shows the building that is currently under upgrade and will raise to level 20 in 1 hours 2 minutes and 50\n        seconds.")]),_c('p',[_vm._v("Weapon Storage is limited to the level of the Headquarters. If the Headquarters is level 10, Weapon Storage\n        can be up to level 10.")]),_c('h4',[_vm._v("3.1.1.3 Alcohol")]),_c('p',[_vm._v("Alcohol is the third resource in DrugWars. It is needed for upgrades of buildings and purchases of units.\n        This resource (just as any other) can get stolen if a player loses a battle but he can keep a portion of it\n        safe by upgrading his alcohol silo. Each building has the following information: level of the building that\n        is\n        over the image, name, cost for the next upgrade in resource under the name and in crypto for instant\n        upgrade,\n        description, and the amount of production on current and next level per day. Each building associated with\n        alcohol requires resources of drugs and weapons for its upgrade while the alcohol silo needs all 3\n        resources.\n      ")]),_c('h2',[_vm._v("3.1.1.3.1 Traditional Distillery")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image34.jpg"}})]),_c('p',[_vm._v("Traditional distillery is the basic alcohol production building that is very cheap to upgrade but also\n        produces the least alcohol in comparison with other buildings. This building is a good thing to start with\n        when you are first starting to play the game so your production increases in time and you can afford to\n        upgrade more useful ones. In the picture above is the example of how on current level 18 a player produces\n        14.68 thousand alcohol every day with this building while he will produce 15.63 thousand weapons on level\n        19.\n        Since the resources are in white, it indicates he has enough of them. If he clicks on the\n        “upgrade” button he would have to wait for 44 minutes and 18 seconds for the upgrade to finish\n        and\n        for the building to rise in level.")]),_c('h2',[_vm._v("3.1.1.3.2 Bar")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image109.jpg"}})]),_c('p',[_vm._v("This building is next in line when it comes to producing alcohol. It is still in the category of fairly\n        cheap\n        buildings when it comes to producing alcohol. In the picture above is the example of how on current level 10\n        a\n        player produces 22.81 thousand alcohol per day with this building while he would produce 25.32 thousand on\n        level 11. Since the resources are in white, it indicates that he has enough of them. If he clicks on the\n        “upgrade” button he will have to wait for 50 minutes for the upgrade to finish and for the level\n        of the building to rise.")]),_c('h2',[_vm._v("3.1.1.3.3 Brewery")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image114.jpg"}})]),_c('p',[_vm._v("Brewery is a building with medium production of alcohol and first to consider if you want to produce\n        alcohol\n        seriously. In the example above, a player has production of 50.86 thousand alcohol with this building per\n        day\n        while he will have that one of 57.02 when he upgrades the building to level 10. Since the resources are not\n        all in white, it indicates he hasn't got enough of them. The only way to upgrade the building is by buying\n        an\n        instant upgrade with crypto or by waiting until he has enough resources for a time managed upgrade.")]),_c('h2',[_vm._v("3.1.1.3.4 Castle")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image66.jpg"}})]),_c('p',[_vm._v("Castle is the second alcohol building with medium production. Costs of her upgrade are much higher than of\n        other buildings but so is her production. In the example above, a player produces 82.43 thousands alcohol\n        with\n        it per day while he would produce 97.07 thousand when he upgrades it to the next level. Cost for upgrades in\n        red indicates that a player hasn't got enough of them. Lack of necessary resources has disabled the upgrade\n        button which now has “miss resources” instead but instant upgrades with crypto are still\n        available.")]),_c('h2',[_vm._v("3.1.1.3.5 Industrial Distillery")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image3.jpg"}})]),_c('p',[_vm._v("Industrial distillery is the most expensive alcohol production building but it is also the one which has\n        the\n        highest production. It massively produces alcohol and you will not go wrong if you focus on upgrading it\n        regularly to ensure your dominance in the game. In the example above, a player produces 181.44 thousands of\n        alcohol with this building per day at level 5 while he would produce 219.8 thousand on the next, sixth\n        level.\n        All his resources needed for the upgrade are in the red which indicates he is lacking them and can only be\n        upgraded by paying with crypto.")]),_c('h2',[_vm._v("3.1.1.3.6 Alcohol Silo")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image61.jpg"}})]),_c('p',[_vm._v("Alcohol silo is the only alcohol building that does not produce alcohol. This building has two functions,\n        it\n        stores alcohol and it keeps it safe. A player CAN NOT have an infinite amount of alcohol. The maximum amount\n        of alcohol that a player may hold is defined with alcohol silo and by upgrading its level, the storage\n        increases. There is a possibility that some other building requires more alcohol than a player can have\n        unless\n        he upgrades the silo enough. For example, a building needs 500K alcohol for the upgrade but the maximum that\n        a\n        player can have is 450K. He will not be able to collect that amount of alcohol if he does not upgrade his\n        alcohol silo accordingly. All alcohol production that is above the storage capacity is NOT saved anywhere\n        and\n        is going to waste to upgrade your storage to make sure you are not producing alcohol for nothing.")]),_c('p',[_vm._v("The second function this building has is the safe. A certain amount of alcohol is being kept safe in the\n        building and can not be stolen in battles when a player loses. With each level, the amount that is safe from\n        stealing increases so a player even when losing a battle will not lose all of his resources.")]),_c('p',[_vm._v("Alcohol Silo is limited to the level of the Headquarters. If the Headquarters is level 10, Alcohol Silo can\n        be up to level 10.")]),_c('h4',[_vm._v("3.1.1.4 Office Production")]),_c('p',[_vm._v("Office buildings do not produce anything but are, in a way, the most important ones to have and upgrade on\n        a\n        regular basis. Buildings have unique specialities and are necessary for other gameplay. Some of them are\n        even\n        required for other actions in the game and a player is not able to access the entire game without them. Each\n        office building has the following info presented: name, level that is over the image, cost for upgrade in\n        resources, description, time needed for upgrade with resources and the cost in crypto for instant upgrade.\n      ")]),_c('h2',[_vm._v("3.1.1.4.1 Headquarters")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image121.jpg"}})]),_c('p',[_vm._v("Headquarters is the first building a player should upgrade and the most important building in the game.\n        This\n        building determines the levels of all other buildings which can not be higher than HQ. If a player has HQ\n        level 20 like in the example above, all his other buildings can be upgraded up to level 20. They can not be\n        upgraded to level 21 until headquarters is upgraded to level 21. The level of HQ also defines the time\n        necessary for upgrades of other buildings and the higher its level, the shorter that time is.")]),_c('h2',[_vm._v("3.1.1.4.2 Training Facility")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image6.jpg"}})]),_c('p',[_vm._v("Training facility is the building that allows players to purchase units for battles. If this building is\n        not\n        at level one at least, a player will not be able to purchase units and attack other players. He will also be\n        vulnerable when others battle against him because he will have no units to defend him. Once this building is\n        built, each new level will reduce the time for purchasing units. If a player decides to purchase units with\n        crypto more frequently than with resources, this building can stay at a low level because buying units with\n        crypto is instant and not time managed which means there is no need for upgrading the building to higher\n        levels.")]),_c('h2',[_vm._v("3.1.1.4.3 Operation Center")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image7.jpg"}})]),_c('p',[_vm._v("Operation center is linked to all resource production buildings. It works in a simple way, all your income\n        in\n        drugs, weapons, and alcohol is raised by 0.5% with each level. Operation center does not influence any of\n        the\n        daily awards, it only raises your daily production of resources. For example, if a player has operation\n        center\n        at level 20, his production will be bigger by 10% because 20 * 0.5 = +10%")]),_c('h2',[_vm._v("3.1.1.4.4 Pantheon")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image49.jpg"}})]),_c('p',[_vm._v("Pantheon is the only building in DrugWars that influences players' activity outside of the game instead of\n        inside. After each battle, a player has an option to share his wins on social networks or DrugWars forum. If\n        he chooses to share it or the forum, he may get a small reward in return. DrugWars team is upvoting DrugWars\n        battle shares on forum and the higher the Pantheon level, the higher the upvote is. Upvotes are NOT\n        mandatory,\n        a player has a chance to be upvoted but will probably not always have that luck. Higher levels of this\n        building do not increase the chances of an upvote but increase its value if the upvote is given. DrugWars\n        team\n        is also upvoting content that is of high quality and related to the game and other players. Posts that are\n        high in value, interesting and of use to other players have bigger chances of getting upvoted than those of\n        simple battle shares.")]),_c('h2',[_vm._v("3.1.1.4.5 Research Center")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image76.jpg"}})]),_c('p',[_vm._v("The Research Center is the building necessary for unit training. Besides simple purchasing of units, each\n        player can train his units in various skills available in training. If a player does not have a Research\n        Center on at least level 1, training will not be available. This building determines the level of every\n        training in a way that the maximum level of each training must match the level of the Research Center. If a\n        player, for example, if the Research Center is at level 5, training can only be upgraded up to level 5.")]),_c('h4',[_vm._v("3.1.1.5 Defense Production")]),_c('p',[_vm._v("Defense production provides defensive systems around your base. Each area of production addresses a\n        specific\n        area of defense.")]),_c('h2',[_vm._v("3.1.1.5.1 Cameras")]),_c('p',[_vm._v("Cameras provide you with information on incoming players to your base.")]),_c('h2',[_vm._v("3.1.1.5.2 Dogs")]),_c('p',[_vm._v("Dogs attack only one time in the second round. They are powerful against Melee units (as the Security\n        System\n        is for Range units).")]),_c('h2',[_vm._v("3.1.1.5.3 Defense Troops")]),_c('p',[_vm._v("These troops defend by attacking after the first round and cannot be killed.")]),_c('h2',[_vm._v("3.1.1.5.4 Emergency")]),_c('p',[_vm._v("The Emergency hospital revives a small percentage of your troops. This is applied once after the fighting\n        has\n        ended.")]),_c('h2',[_vm._v("3.1.1.5.5 Security System")]),_c('p',[_vm._v("An advanced security system for Range units (as Dogs are for Melee) that attacks in even numbered rounds,\n        starting at round two to round six.")]),_c('h2',[_vm._v("3.1.1.5.6 Hidden Mines")]),_c('p',[_vm._v("A first line of defense, using splash damage triggered at the first round only.")]),_c('h3',[_vm._v("3.1.2 Units")]),_c('p',[_vm._v("Units are expendable pieces of the DrugWars game. They are purchased, trained and used in battles. When a\n        unit dies in battle, the player's number of that unit decreases by the number that had just died but he can\n        buy more as often and as much as he wants. There is no limit to quantity or quality of the units.To be able\n        to\n        purchase units, a player must first have the Training facility building and if he wants to train them then\n        he\n        needs to have the Research Center. Both of those buildings are office buildings and can be upgraded\n        accordingly.")]),_c('p',[_vm._v("Each unit is unique in her skill set and cost. Some units are malee which means they are for close combat\n        while some are range or battling from a distance. Units are the only thing in the DrugWars game that have a\n        number over the picture that does not stand for level. Whenever you see a number over an image in DrugWars\n        with buildings or training, it represents the level while with units, it represents the number of units that\n        a\n        player currently has.")]),_c('p',[_vm._v("Each unit has the following information next to it: picture and number of units a player has over it,\n        description and info about unique abilities, cost in resources, supply, offense and resistance skills, time\n        needed for time managed recruitment, and the cost for recruitment paid with crypto. Time needed for time\n        managed recruitment can be shortened with higher levels of Training Facility building.")]),_c('h4',[_vm._v("3.1.2.1 Recruiting Units")]),_c('p',[_vm._v("To recruit (purchase) units, a player needs to have the necessary resources (different for different\n        units),\n        enter the amount he wants to recruit in the white space and click on the green recruit button. If resources\n        are missing, the green recruit button will be replaced with black one that says “missing\n        resources”. A player can, at any time, choose to recruit his units by paying for them with crypto in\n        which case the resources are not necessary and the purchase is instant and not time managed.")]),_c('h2',[_vm._v("3.1.2.1.1 Hobo")]),_c('p',[_vm._v("Hobo is the cheapest unit to recruit and the only one that requires only one resource for purchase. This\n        unit\n        has no defense and will die if he meets his enemy at the end of the first round. Hobos are fairly slow and\n        can\n        not carry much which means they are not very good for sending resources to other players."),_c('img',{attrs:{"src":"/img/help/image9.png"}})]),_c('h2',[_vm._v("3.1.2.1.2 Rowdy")]),_c('p',[_vm._v("Rowdy is a unit that fights in groups of four so be sure to always send rowdies to battle in a number that\n        can be divided by 4 to assure their maximum efficiency. Rowdy has no defense against fire and chemical\n        weapons\n        and can defend only against physical damage and bullets."),_c('img',{attrs:{"src":"/img/help/image125.png"}})]),_c('h2',[_vm._v("3.1.2.1.3 Super Bouncer")]),_c('p',[_vm._v("Super bouncer is a medium strong unit for close combat but has a fairly weak defense. His special ability\n        is\n        his shield which he can use once and obtain +200 more health which makes him harder to kill in battles."),_c('img',{attrs:{"src":"/img/help/image54.png"}})]),_c('h2',[_vm._v("3.1.2.1.4 Big Mama")]),_c('p',[_vm._v("Big Mama is the only unit with healing abilities because she can heal up to 5 units for 200HP in every\n        round.\n        She is fairly slow but has medium offense and defense and is not an easy unit to kill."),_c('img',{attrs:{"src":"/img/help/image19.png"}})]),_c('h2',[_vm._v("3.1.2.1.5 Knifer")]),_c('p',[_vm._v("Knifer is a solid unit when it comes to his skills in battle but be careful because this unit does not\n        follow\n        rules. This means that he does not follow priority in battles and can attack sooner or later than he is\n        supposed to."),_c('img',{attrs:{"src":"/img/help/image16.png"}})]),_c('h2',[_vm._v("3.1.2.1.6 Gunman")]),_c('p',[_vm._v("Gunman is a pretty strong range unit that takes out melee units from a safe distance. His aim is almost\n        perfect so his accuracy ensures bonus damage. His defense is low, however, because he is not very good at\n        defending himself in close combat."),_c('img',{attrs:{"src":"/img/help/image25.png"}})]),_c('h2',[_vm._v("3.1.2.1.7 Lawyer")]),_c('p',[_vm._v("Not all men are born for fighting, some have other useful skills. If you send a lawyer in battle, he will\n        reduce the amount your opponent has in his safe by 0.01% which means you can steal even some resources that\n        your opponent is not expecting you too, up to 5% that are in his safe. This unit is a genius in battle but\n        will hide behind big mama to protect himself."),_c('img',{attrs:{"src":"/img/help/image77.png"}})]),_c('h2',[_vm._v("3.1.2.1.8 Spy")]),_c('p',[_vm._v("Spy is not a good choice for battles but an excellent one for spying. Before starting a normal attack, a\n        player can send only a spy in battle and his spy will return with valuable information about the opponent\n        such\n        as buildings and number of units an opponent has. With knowing what units will wait for him, players can\n        strategize and choose which units to send in battle more carefully."),_c('img',{attrs:{"src":"/img/help/image29.png"}})]),_c('h2',[_vm._v("3.1.2.1.9 Sniper")]),_c('p',[_vm._v("Sniper is an excellent choice for battles. He is a range unit that kills your opponents units from a\n        distance\n        with increases to his damage with each round. His damage will be greater by 1.5*x where x is the number of\n        rounds which means he will deal the most damage in the last round."),_c('img',{attrs:{"src":"/img/help/image122.png"}})]),_c('h2',[_vm._v("3.1.2.1.10 Hitman")]),_c('p',[_vm._v("Hitman is a strong range unit that a player can not go wrong with. This unit has a special ability of a\n        multiple hit because he takes down two targets at once with his two guns, one in each hand. He is fast and\n        with excellent offense and defense skills."),_c('img',{attrs:{"src":"/img/help/image40.png"}})]),_c('h2',[_vm._v("3.1.2.1.11 Ninja")]),_c('p',[_vm._v("Ninja is the second female unit. She has cat-like reflexes and you can say she has more than one life just\n        as\n        a cat. Ninja can dodge two deadly hits and it doesn't even matter how strong they are. This, in combination\n        with her great offense skills, makes her quite powerful. "),_c('img',{attrs:{"src":"/img/help/image62.png"}})]),_c('h2',[_vm._v("3.1.2.1.12 Bazooka Guy")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image70.jpg"}})]),_c('p',[_vm._v("Bazooka guy is a good unit to have if you want to up your chances of winning a battle. This unit will hit\n        not\n        one, not two but 10 enemies at once and is generally very good with all his offense skills.")]),_c('h2',[_vm._v(" 3.1.2.1.13 Occupation Troop")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image92.jpg"}})]),_c('p',[_vm._v("This is the only unit which is not used in battles. To occupy a territory and create a new base, a player\n        needs to send this unit in to make room. This unit is slow but can carry a lot and is safe from attacks so\n        if\n        you want to expand your empire and create more bases, send these in. The Occupation Troop is disposable, it\n        creates the new base and is consumed into it. Once you send it to create a new base, the unit is gone. Your\n        new base is created instantaneously.")]),_c('h2',[_vm._v("3.1.2.1.14 Mercenary")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image97.jpg"}})]),_c('p',[_vm._v("Mercenary is the most expensive unit but also the strongest one. This unit has exceptional defense and\n        offense skills and will help you win in battles. His unique skills is his bulletproof vest which helps him\n        to\n        obtain back 25HP when re reaches 0 so just as your opponent will think your mercenary is dead, surprise, no\n        he\n        is not.")]),_c('h4',[_vm._v("3.1.2.1 Training units")]),_c('p',[_vm._v("Unit training is an option that every player can take advantage of. To be able to train their units,\n        players\n        first need to have the Research Center building and all training can go as much up in levels as the Research\n        Center. If your Research Center is level 10, your training levels are limited to level 10. Some training\n        affects all units while others are specifically designed for some or only specific type of units. Each\n        training has the following information next to the image: description, unique advantage, cost in resources,\n        time required for time managed upgrades and the price for instant upgrade in crypto.")]),_c('h2',[_vm._v("3.1.2.1.1. Routing")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image64.jpg"}})]),_c('p',[_vm._v("Routing is training of all units in speed. With each level, all units will have increased speed by 0.5%.\n      ")]),_c('h2',[_vm._v("3.1.2.1.2. Close Combat")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image95.jpg"}})]),_c('p',[_vm._v("Close combat is training of only melee units (those who fight in close combat) in physical damage. With\n        each\n        level, melee units will increase the physical damage they give in battles by 1%.")]),_c('h2',[_vm._v("3.1.2.1.3. Introduction to pistol, rifle, and shotgun")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image26.jpg"}})]),_c('p',[_vm._v("This training is only for range units and it has the same function as close combat has for melee. Pistol,\n        rifle & shotgun will increase the physical damage units give in battles by 1% with each level of\n        training.\n      ")]),_c('h2',[_vm._v("3.1.2.1.4. Information procurement")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image104.jpg"}})]),_c('p',[_vm._v("Information procurement is training for all types of units that removes the fog of war.")]),_c('h2',[_vm._v("3.1.2.1.5. Sniper trains")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image17.jpg"}})]),_c('p',[_vm._v("Sniper Trains is (as the name suggests) training for only snipers. With each level of training, snipers\n        will\n        get to give 1% more of physical damage to opponents.")]),_c('h2',[_vm._v("3.1.2.1.6. Spirit wine")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image90.jpg"}})]),_c('p',[_vm._v("Spirit wine is training specifically designed for the Hobo unit. Since this unit is fairly weak and dies\n        when\n        he meets his enemy at the end of the round, training it seems logical. With each level of training Hobos\n        will\n        give 1% more physical damage to their opponents.")]),_c('h2',[_vm._v("3.1.2.1.7. Coordination")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image45.jpg"}})]),_c('p',[_vm._v("Coordination is for all units. With each level of training, units will have increased offensive and\n        defensive\n        power by 1%.")]),_c('h2',[_vm._v("3.1.2.1.8. Chemical training")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image105.jpg"}})]),_c('p',[_vm._v("Chemical training is for all units. With each level of training, units will produce greater chemical damage\n        to their opponents by 1%.")]),_c('h2',[_vm._v("3.1.2.1.9. Weapon master")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image14.jpg"}})]),_c('p',[_vm._v("Weapon master is training for all unit types. With each level of training, units will give greater weapon\n        damage by 1% to their opponents.")]),_c('h2',[_vm._v("3.1.2.1.10. Fire training")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image71.jpg"}})]),_c('p',[_vm._v("Fire training is for all unit types. This training will increase fire damage by 1%.")]),_c('h2',[_vm._v("3.1.2.1.11. Protection")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image73.jpg"}})]),_c('p',[_vm._v("Protection is training for all unit types. With each level of this training, all units will be better at\n        defense by 1%. [NOTE: image shows only 0.5%]")]),_c('h2',[_vm._v("3.1.2.1.12. Bomb construction")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image120.jpg"}})]),_c('p',[_vm._v("Bomb construction is training specifically for the Bazooka unit. With each level of training Bazookas will\n        give 1% more damage to their opponents.")]),_c('h2',[_vm._v("3.1.2.1.13. Psychological training")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image30.jpg"}})]),_c('p',[_vm._v("Psychological training is specifically designed for Elite units (Big Mama, Knifer, Ninja, and Mercenary).\n        With each level, damage that Elite units produce will be higher by 0.5%.")]),_c('h2',[_vm._v("3.2 Player location on the map"),_c('img',{attrs:{"src":"/img/help/image108.jpg"}})]),_c('p',[_vm._v("Once you click on the Map in the left menu, a rotating Earth image will appear in the main frame. You\n        will notice how it has a lot of small hexagons on it. Each hexagon is one territory and every such territory\n        has 225 possible base locations. You will also notice how those hexagons are in different colors. There is\n        black, green, yellow, orange, red, and blue. Those colors indicate how dangerous a specific territory\n        is\n        where black is empty, green means that there is an inexistent or low danger, yellow is moderate and orange\n        and\n        red considerable and even high. Only blue locations are different because they are implicating how there\n        will\n        soon be missions on them.")]),_c('p',[_vm._v("Every player has one free base location to choose. That primary base has no required resources or\n        obligations\n        for a player in any way, it is completely free and simple to choose. Every player can set up to 5 bases if\n        he\n        wishes (they are not mandatory) but those 4 additional ones require some investment in the terms of\n        purchasing a special unit that will establish them.")]),_c('h3',[_vm._v("3.2.1 Choosing primary base location")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image42.jpg"}})]),_c('p',[_vm._v("When you log into the DrugWars game, it will tell you how you need to choose a location on the map\n        first. Until you do that, you will not be able to play the game or see the resources that you have. You can\n        either click on the green link under your image that says \"SELECT THE LOCATION\" or click on the\n        map in the left menu. Both will get you to the same place, they will open the rotating world map with\n        hexagons on it.")]),_c('p',[_vm._v("To pick your primary base, click on the map in the left menu. When the world map appears in the main frame\n        you can either watch how it spins and look at different territories or you can click and drag your mouse to\n        rotate it manually. When you click on any hexagon territory, a small window will pop-up next to it with the\n        information on how many bases are already in that territory and how many are free. Keep in mind that it is\n        giving you information about bases, not players. If it says there are 4 bases there, it can mean that those\n        4\n        bases can be owned by 1,2,3, or 4 different players. When you have chosen your preferred territory, click on\n        the hexagon that represents it.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image116.jpg"}})]),_c('p',[_vm._v("Once you have clicked on the territory, base locations will open in a grid. Every territory grid has 225\n        base\n        locations. Those which are in red color have already been claimed by another player and you can find out who\n        by clicking on them. Those which are in black color are free and available for you to choose them as your\n        primary base location.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image44.jpg"}})]),_c('p',[_vm._v("From those available black base locations, you need to choose one for your primary base. When you click on\n        the one you desire, you will need to confirm it by clicking on the blue button CHOOSE AS PRIMARY BASE. Your\n        base will from then on be colored green in the base grid of that territory and the information about your\n        primary base location will be under your image and in the right corner of the main frame. Those players that\n        will have more than one location will always know which base they are on by reading what it says in that\n        right\n        corner or under their image. The first number is territory while the second one is the base in that\n        territory.\n        In the example below, the primary base is territory 442 and location 1.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image36.jpg"}})]),_c('h3',[_vm._v("3.2.2 Choosing secondary base locations")]),_c('p',[_vm._v("Every player can have up to 9 additional bases (a total of 10, 1 primary and 9 additional). The process is\n        very similar to choosing the primary base except it requires a special unit called Occupation Troop. A\n        player\n        must first recruit this unit to be able to get an additional base with it.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image28.jpg"}})]),_c('p',[_vm._v("Occupational troop has the potential to create a new base. If you use it for that purpose, it will\n        transform\n        itself into the base which means that you will not have that unit anymore. The unit will not return once the\n        base is set because the unit has become the base itself. In addition to occupying territory and creating\n        bases, this unit can also transport resources from one base to another and station units. This is a good\n        thing\n        to do in order to assure enough resources in additional bases to start building upgrades and unit\n        recruitment.\n      ")]),_c('p',[_vm._v("When you choose your base location (in the same way as for the primary one) and click on a base in the\n        desired territory, the blue button will now say CREATE NEW BASE. Once you click on it, it will divert you to\n        choose the unit for that action. You need to select one Occupational troop by entering the number in the\n        blank\n        space and clicking on the image of the unit (just as you would select units for battle). Your coordinates\n        will\n        already be set but you can change them if you wish and you need to enter the name of your new base. When you\n        have your occupational troop selected, coordinates of the base, and the new base name, click on\n        OCCUPY and your base will be created.")]),_c('p',[_vm._v("Each player can have 10 bases, so you can create up to 9 additional bases in addition to your primary base.\n      ")]),_c('h3',[_vm._v("3.2.3 Production of buildings and units")]),_c('p',[_vm._v("Each base has its own production, buildings, and units. From the Headquarters to Alcohol Silo, players can\n        upgrade their buildings in every base individually. It is like playing five games at once if you have 5 base\n        locations. Those upgrades can be simultaneous so while you are upgrading the Cocina in one base there is no\n        problem in upgrading Black Market in another and the Bar in the next. The same goes for recruiting units.\n      ")]),_c('p',[_vm._v("Be careful what you are building and where. Before starting any upgrade make sure that you are doing it in\n        the base that you want. You can always see which base you are on because it is stated under your image and\n        in\n        the upper right corner of the main frame. To switch between locations, simply click on the small arrow next\n        to\n        the primary base to expand the list of bases and click on the one you want. Once you do that, the chosen\n        base\n        will be the one you are currently in. By producing resources in more than one base and transporting them,\n        you\n        will not only collect them faster and be able to upgrade more buildings but also have bigger drug production\n        and as a result, higher daily rewards. Your information about resource production in the top banner will\n        change according to the base you are currently on but you can see your total production from all bases if\n        you\n        click on overview in the left menu. That total production is what your daily prize is based upon.Your\n        daily prize is for your total drug production from all bases added together.")]),_c('p',[_vm._v("Since you can station units from one base in another one, it would be silly that 5 of your hobos were\n        trained\n        to level 6, for example, and another 15 to level 2. All your units are at the same level of any training at\n        all times. This means that training will not be different from one base to another. It will be the same. By\n        upgrading training in one base, it will automatically rise in level in all base locations once the upgrade\n        is\n        finished.")]),_c('h2',[_vm._v("3.3 Battles")]),_c('p',[_vm._v("Battling against other players is the key feature of the DrugWars game. By attacking other players and by\n        defending themselves against attacks, players can get rewarded in more than one way. Each battle will assure\n        that some amount of resources from the opponent goes to the winner of the fight and based on how much he\n        reduced the DrugWars population, players will receive additional daily reward in crypto. You can read about\n        the rewards from battles here Battle prize.")]),_c('h3',[_vm._v("3.3.1 Preparing for a battle")]),_c('p',[_vm._v("A player can not attack other players if there are no units to go to that attack. To be able to attack,\n        there\n        first need to be units present. In order to have a bigger chance of victory, those units can be trained and\n        the target should be chosen carefully. Additionally, there is a fight simulator that can help players see in\n        advance how the fight is going to look like. If the opponent does not change his unit number and defensive\n        buildings in the meantime, the attacker can expect the battle to go as the simulator predicted.")]),_c('h4',[_vm._v("3.3.1.1 Unit production and training")]),_c('p',[_vm._v("To be able to participate in battles, players need to produce their units and train them if they want to\n        assure their skills are even better. Unit production can be at every single base while unit training is the\n        same for all bases. To learn more about unit production and training, read Units.")]),_c('h4',[_vm._v("3.3.1.2 Choosing targets")]),_c('p',[_vm._v("The best way to assure your target is not too strong for you is to spy on it with the unit Spy. This unit\n        will bring you back a lot of valuable information about how many units your opponent has and some other\n        useful\n        info. If that player does not produce more units in the meantime, you can assume he has what your Spy told\n        you. Simply attack a player with the Spy unit and wait for it to return. To attack a player, you need to\n        enter\n        the location of his base you will attack or find the base on the map and attack from there.")]),_c('h4',[_vm._v("3.3.1.3 The fight simulator")]),_c('p',[_vm._v("Once you have learned how many units your opponent has, and you know which units you will send into battle,\n        access the fight simulator here: https://simulator.drugwars.io/ and enter the numbers of units you both\n        have. When you click on submit, the simulator will give you information about the battle process and what\n        happens at each round. You will know who the winner will be of that potential fight too. To assure your\n        victory, play with the simulator a bit and enter different unit numbers for your attack. This way you will\n        see\n        different scenarios that may take place. Keep in mind that since all battles have a time delay, the\n        simulator\n        information will be true only if your opponent does not make any changes to his units in the meantime.")]),_c('h4',[_vm._v("3.3.1.4 Unit order and grouping")]),_c('p',[_vm._v("When starting a fight, a player needs to know how there is an attack order among units. That order can be\n        seen in units.json file of DrugWars GitHub and it goes like this: Spy, Hobo, Rowdy, Bouncer, Knifer,\n        Big\n        Mamma, Lawyer, Ninja, Gunman, Sniper, Hitman, Bazooka, Mercenary. This means that Bouncers, for example,\n        will\n        always attack before Gunman and so on.")]),_c('p',[_vm._v("The same units are grouped together, they fight together and their progress in the simulator is shown one\n        after another. If a player goes into battle with 2 Super Bouncers, those 2 Super Bouncers will attack\n        together\n        and their information will be presented one line at a time.")]),_c('h3',[_vm._v("3.3.2 Battle process")]),_c('p',[_vm._v("The battle takes place in rounds and it ends either when all the units from one side are dead or until all\n        rounds are finished. Attacking units can only attack one of the opposing team's groups per round and both\n        the\n        attack and the defend side units are in the battle based on their unit order. At each round the unit may\n        lose\n        its HP based on its defense and the attacker strength on it. For example, a layer with 2000 HP and 50 DEF\n        can\n        take 450 DMG from Big Mamma and his HP will drop to 1550 HP. If there are 5 Big Mammas, they will deal\n        5*450=2250 DMG and the lawyer will be dead.")]),_c('p',[_vm._v("Since damage goes both ways, be sure that lawyer units will be protected by Big Mamma units. He will assign\n        120 DMG to Big Mamma and get her HP from 21000 to 20880. To simplify, let us see what happens with one\n        Lawyer\n        and one Big Mamma:")]),_c('p',[_vm._v("Lawyer: HP 2000, attack 200, and defense 50")]),_c('p',[_vm._v("Big Mamma: HP 21000, attack 500, and defence 80")]),_c('p',[_vm._v("Big Mamma attacks Lawyer with an attack of strength 500 but since he has a defense of 50, the attack\n        he\n        gets is 450 which means his HP gets from 2000 to 1550. At the same time, while defending himself, he deals\n        Big\n        Mamma damage of 200 but since Big Mamma has defence of 80, she gets 120 damage (because 200-80=120) and her\n        HP\n        is now 20880.")]),_c('p',[_vm._v("Each round can be seen as the attack on one defending unit where the attacking units take turns at\n        attacking while the defending unit gives them some damage during his defending. For example, if there are 2\n        Bouncers, 2 Big Mammas and 1 Knifer attacking 5 Rowdies and 5 Lawyers, one round will be the attack on\n        Rowdies\n        and another round will be the attack on Lawyers. All Bouncers, Big Mammas and Knifer will attack Rowdies and\n        assign damage, Rowdies will give them damage during defense. All Bouncers, Big Mammas and Knifer will attack\n        Lawyers and assign damage, Lawyers will give them damage during defense. If there are units still left\n        alive,\n        the process continues. This, of course, depends on the training of the units too since different\n        training\n        will help units be better at attacking and defending themselves.")]),_c('h3',[_vm._v("3.3.3 Battle rewards")]),_c('p',[_vm._v("The winner of the battle gets to loot the loser. The amount of resources the winner can loot is defined\n        with\n        how much of those resources the loser has and is not in the safe just as it is defined with the winning\n        units\n        that have survived. Each unit has a carry capacity and only surviving units can carry loot back home. For\n        example, Super Bouncer can carry 400 and if there are 5 alive after battle, they can carry 2000 resources.\n        Winner can loot all 3 resources, drugs, weapons, and alcohol.")]),_c('h3',[_vm._v("3.3.4 Sharing Battles")]),_c('p',[_vm._v("Once a battle is over, players can share it in different social media networks: DrugWars forum, facebook,\n        reddit, twitter, by creating a qr code, VK, and on Telegram. You first need to click on show more details\n        when\n        scrolling through previous battles and just under your rewards there will be a series of links you can click\n        on to share your battle.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image56.jpg"}})]),_c('h2',[_vm._v("3.4 Heist")]),_c('p',[_vm._v("Every player has an opportunity for extra daily income from Heirs. You can invest your drugs and based on\n        your investment in comparison from other players, get crypto in return. A certain percentage of total daily\n        prizes goes to heist (usually 2%) which is somewhere from 10K to 15K. Players who invest more in the heist\n        will get a bigger piece of that prize while those who invest less will get less. Since you can invest as\n        many\n        times as you want during the day, information about your prize will be temporary because other players are\n        also investing which means you have to keep a close eye and invest more accordingly if you want a bigger\n        reward.")]),_c('h2',[_vm._v("3.5 Jobs"),_c('img',{attrs:{"src":"/img/help/image21.png"}})]),_c('p',[_vm._v("Jobs are an important aspect of the game. By doing jobs, you fight the DRUGWARS NPC (their army / units)\n        and\n        take rewards for doing so. While your production of drugs, weapons and alcohol raises your levels for\n        upgrades\n        and recruiting units, completing jobs brings in much greater rewards. Each player is allowed to start 15\n        jobs\n        per day, the day timer can be seen on the upper right portion of the screen. The timer starts a new day at\n        24\n        hours and ends once it reaches 0 (zero).")]),_c('p',[_vm._v("When doing jobs, whether solo jobs or gang jobs, the units used do not need to have the capacity to carry\n        the\n        rewards back. This differs from fights, where your attacking army can only bring back up to its capacity.\n        This\n        value can be seen under the unit’s Defense -> Carry attribute.")]),_c('p',[_vm._v("TIP: All units can carry resources, but if you plan to carry larger amounts, like 50k-100k+, you really\n        need\n        to utilize the AH-64 Apache unit which can carry up to 30k combined resources.")]),_c('h3',[_vm._v("3.5.1 Jobs vs Gang Jobs")]),_c('p',[_vm._v("Jobs are individual, or solo, jobs where the job is done by the individual player and the rewards are given\n        to the single player. A Gang Job is done by one to many gang members and the rewards are shared amongst the\n        daily active members of the gang.")]),_c('h4',[_vm._v("3.5.1.1 Jobs")]),_c('p',[_vm._v("Individual Jobs are the #1 way to grow yourself in addition to letting your resources build. The rewards\n        from\n        these jobs are given to the individual player who does them. To do a job, you need to send an army of units\n        capable of defeating the DRUSWARS NPC army. You can test this in the fight simulator\n        (https://simulator.drugwars.io/).")]),_c('h4',[_vm._v("3.5.1.2 Gang Jobs")]),_c('p',[_vm._v("Whilst one to many members can participate in a Gang Job, oftentimes a single gang member can and will\n        complete a job on their own. This is to the benefit of the gang as it usually results in the single member\n        losing more resources than they gain. Gang Jobs promote teamwork and comradery within the gang. Gang Jobs\n        are\n        a great way for the gang to help each other, especially the younger and newer members.")]),_c('p',[_vm._v("TIP: It is important to remember that the Gang Job is shared amongst all daily active members. So the more\n        members you have, the more members take a share. The share is distributed evenly, so those who participate\n        do\n        not receive any more than those who do not.")]),_c('h1',[_vm._v("4. Gangs")]),_c('p',[_vm._v("DrugWars can be played individually, as a single player with no ties to anyone. It can also be played with\n        cooperation with others in groups that are called gangs. A player does not have to be a part of the gang to\n        be\n        able to play DrugWars but if he or she so chooses, there is no option of being in more than one gang. Once\n        you\n        are in a gang, you can still play as you would if you were not a part of it. You do not lose anything, you\n        only gain. Different gangs have different rules of play and operate in various styles when it comes to wars\n        and alliances with other gangs and there are many benefits a player gets by being a part of the gang. If you\n        are in a gang, you do get a certain level of protection by other gang members in battles and your production\n        of resources and units gets faster by gang buildings so your individual play benefits as well. You can\n        create\n        your own gang or join an existing one that is open to new members. There is currently a limit to how many\n        members a gang can have that is set to 100. Each gang has 3 different roles for members, boss, capo, and\n        soldier. There is only one gang boss who assigns capo roles to soldiers. The boss upgrades gang buildings\n        and\n        has the final word in everything, capos are above soldiers and below boss in the chain of command while the\n        soldiers are the lowest in the hierarchy. To learn where the gang information is located in the game and\n        read\n        the instructions of creating or joining the gang, click here: GANGS.")]),_c('h2',[_vm._v("4.1 Creating a gang")]),_c('p',[_vm._v("Instructions for creating a gang are located here: Create gang. If you have enough resources, you can start\n        your own gang, name it as you wish and assign a logo to it. DrugWars discord server is at your service and\n        will provide a chat room for your gang where you can talk to other members, discuss strategy and gameplay.\n        This is also the place where you can go into more details when it comes to approving applications of your\n        new\n        potential members. When you create a gang, you become its gang boss and can assign capo roles to your\n        soldiers\n        which will give them more power and authority over your gang. Consider alliances with other gangs and build\n        a\n        strong gang before starting wars with others to assure your victory.")]),_c('h2',[_vm._v("4.2 Joining a gang")]),_c('p',[_vm._v("Instructions for joining a gang are located here: Joining gang. When you are a gang member (boss, capo or\n        soldier) there are many benefits for your individual play. As a single player, you are much stronger if you\n        have a gang to protect your back and you are better at production too. Gang members will usually attack your\n        enemies and your production rates will get higher with higher levels of gang buildings.")]),_c('p',[_vm._v("If you are currently in a gang and wish to join another, you apply to the new gang. If you are accepted,\n        you\n        will be switched to the new gang upon acceptance. When you join a gang you can’t join another one for\n        48\n        hours.")]),_c('h3',[_vm._v("4.2.1 Gang buildings")]),_c('p',[_vm._v("Each Gang building impacts every player in the gang. The higher the building, the bigger your benefits are.\n        Gang members can easily donate their resources to a particular building by inputting the amount and clicking\n        on send resources. Each building has info about its purpose, resources needed for every next level upgrade\n        and\n        the percentage of resources that have already been collected by donations from gang members. Those\n        percentages\n        are also depicted by a pie chart surrounding the resource image where the percentage of received resources\n        is\n        green and the resources missing are yellow. Only gang boss can upgrade the buildings by clicking on upgrade\n        and it can be done only when all 3 resources are at 100%, otherwise, it will say missing resources where the\n        upgrade button is.")]),_c('h4',[_vm._v("4.2.1.1 Gang headquarters")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image96.jpg"}})]),_c('p',[_vm._v("Gang headquarters is your main gang building and it is listed first when you click on buildings. Every\n        other\n        building cannot be higher than the level of your headquarters so when you see “HG upgrade\n        required” where your upgrade button next to some building should be, it means that you first need to\n        upgrade the HQ and then the building you wanted.")]),_c('h4',[_vm._v("4.2.1.2 Embassy")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image39.jpg"}})]),_c('p',[_vm._v("The Embassy allows the Gang to view relations with other Gangs. You can see Gangs you are at war with,\n        allies\n        with and the overall state of relationships.")]),_c('h4',[_vm._v("4.2.1.3 Military Academy")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image33.jpg"}})]),_c('h4',[_vm._v("4.2.1.4 Scientific Lab")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image106.jpg"}})]),_c('p',[_vm._v("The scientific lab is the gang building that influences every gang member's individual drug production.\n        With\n        each level, your individual drug production will be higher by 0.25%.")]),_c('h4',[_vm._v("4.2.1.5 Weapon Development Center")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image111.jpg"}})]),_c('p',[_vm._v("Weapon Development Center is the gang building that influences every gang member's individual weapon\n        production. With each level, your individual weapon production will be higher by 0.5%.")]),_c('h4',[_vm._v("4.2.1.6 Distillery School")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image67.jpg"}})]),_c('p',[_vm._v("Distillery School is the gang building that influences every gang member's individual alcohol\n        production. With each level, your individual alcohol production will be higher by 0.5%.")]),_c('h3',[_vm._v("4.2.2 Deposit")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image20.jpg"}})]),_c('p',[_vm._v("Deposit page will show you how much of each resource type has every player donated to each building.\n        Buildings are listed with gang member names underneath them and the amount of resources they have donated.\n      ")]),_c('h2',[_vm._v("4.3 Diplomacy")]),_c('p',[_vm._v("Every gang has an option to join alliances and wars with other gangs. When you click on Diplomacy you will\n        see every other gang in your main frame with red and green options next to them.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image22.jpg"}})]),_c('p',[_vm._v("The green button will say OFFER ALLIANCE and the red one will say DECLARE WAR. If you are already at\n        war\n        or have an alliance with a certain gang then those two buttons will be grey with the additional blue one\n        under\n        them that will say STOP. Only the boss and the capos can make those decisions.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image4.jpg"}}),_vm._v(" "),_c('img',{attrs:{"src":"/img/help/image38.jpg"}})]),_c('h3',[_vm._v("4.3.1 Wars")]),_c('h3',[_vm._v("4.3.2 Alliances")]),_c('h2',[_vm._v("4.4 Jobs")]),_c('h2',[_vm._v("4.5. Leaving a gang"),_c('img',{attrs:{"src":"/img/help/image35.png"}})]),_c('p',[_vm._v("To leave the Gang of which you are a member, under Gangs, select your gang and find your user name/profile.\n        Click on “Leave Gang”.")]),_c('h1',[_vm._v("5. Sharing resources with other players")]),_c('p',[_vm._v("You can share your resources with other players by transporting drugs, weapons, alcohol or crypto by your\n        units. When you click on MISSIONS in the left menu, you will see TRANSPORT in the menu above the main frame.\n        Click on it to see the transport content of the game.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image8.jpg"}})]),_c('p',[_vm._v("All of your current units will be listed on the left. You will see a number under each unit that tells you\n        how much of them you have. To transport resources, you first need to select those units you want to carry\n        your\n        resources. Each unit type has a different carry level (you can see the carry levels of every unit in\n        Bootcamp). To select the unit you wish to participate in the transfer, change the number under it and click\n        on\n        the plus size on the image. You can add as many different units as you want but you need to select at least\n        one. For this example, we will select 3 Hobos and 1 Ninja.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image58.jpg"}})]),_c('p',[_vm._v("Hobo has the carry ability of 10 and Ninja has the carry ability of 150. Since we have selected 3 hobos, 10\n        multiplied by 3 is 30 which is added to 150. We get a total carry of 180 resources. This means that we can\n        transport 180 resources. Those resources can be divided in any way that math allows through the use of\n        integers. You can send all 4 resources, just one or any other combination. For example, it can be 180 drugs\n        or\n        50 drugs + 30 weapons + 100 alcohol or 10 drugs + 10 weapons + 10 alcohol + 150 crypto or any other\n        mathematically allowed distribution. All you need to do now is enter the name of the player you wish to\n        transport, write a message and click on TRANSPORT. When your units deliver those resources they will come\n        back\n        to you so do not worry about losing them. Once the resources are delivered, your resources will be lesser by\n        the amount that you gave away.")]),_c('p',[_vm._v("If you are a gang member and click on MY GANG to see all the members, you will see there is a blue\n        TRANSPORT button next to each gang member. When you click on it, it will divert you to transport\n        content\n        that we have covered above.")]),_c('h1',[_vm._v("6. Simulator")]),_c('p',[_vm._v("You can access the fight simulator by going to https://simulator.drugwars.io/. The left side of the screen\n        is\n        your units and training numbers. The right side is either 1) the opponent unit, training and defense numbers\n        (if you are attacking another player), or 2) the units of the NPC (if you are doing a job).")]),_c('p',[_vm._v("Clicking the simulator links within the game will often pre-fill values for you. For example, clicking on\n        “Open in the simulator” after you have started a job will automatically fill your units and\n        trainings and also the NPC units.")]),_c('p',[_vm._v("The simulator is an exact depiction of the game play fight. What it shows is how the fight will resolve.\n      ")]),_c('h1',[_vm._v("7. Card collecting")]),_c('h1',[_vm._v("8. F.A.Q.")]),_c('h2',[_vm._v("8.1 Are bots allowed in the game?")]),_c('p',[_vm._v("DrugWars does not tolerate any bot automating any action in the game. This gives unfair advantage to some\n        players and exploits the game. Bot usage is strictly monitored. Abusers will be banned immediately upon\n        being\n        flagged for abuse. You have the right to one appeal as to why you were flagged to explain why it may be\n        false.\n        After, if the infraction is the same, you are banned.")]),_c('h2',[_vm._v("8.2 Why is my shared battle not upvoted?")]),_c('p',[_vm._v("Higher Pantheon building levels will produce higher upvote values but not all shared battles will be\n        upvoted.\n        Sharing a battle gives you A CHANCE of being upvoted. Upvoting battles is not mandatory or guaranteed.")]),_c('h2',[_vm._v("8.3 Is being in the gang better than playing solo?")]),_c('p',[_vm._v("The answer depends on what kind of style a player prefers. Gang members play together and socialize more,\n        offer protection and go on missions together. Individual players can enjoy the game and play it but some\n        features of the game are only available to gangs since they are specifically designed for them and require\n        more players.")]),_c('h2',[_vm._v("8.4 Can I have more than one DrugWars account?")]),_c('p',[_vm._v("Yes, you can. If you have more than one Steem account, you can play DrugWars with each one of them\n        individually. Additionally, you can create a DrugWars account by logging in with your social media account.\n      ")]),_c('h2',[_vm._v("8.5 Why are daily rewards going up and down?")]),_c('p',[_vm._v("Daily rewards are dependent on investments from all players when they purchase instant upgrades with\n        crypto.\n        There will be days when a lot of players will purchase upgrades and the prize will get bigger just as there\n        will be days when more players will purchase upgrades with resources and the prize will be smaller.")]),_c('h2',[_vm._v("8.6 My daily rewards are dependent on which base?")]),_c('p',[_vm._v("You will get your daily prize based on your total drug production which is calculated by adding drug\n        productions from all the bases. You can see your total drug production in the Overview.")]),_c('h2',[_vm._v("8.7 How do I know which base I am currently on?")]),_c('p',[_vm._v("There is information under your logo image and in the upper right corner of the main screen. Read Player\n        location on the map")]),_c('h2',[_vm._v("8.8 Why are my resources in red and will not go higher than a certain amount?")]),_c('p',[_vm._v("You can not have unlimited amounts of resources. The capacity of each resource is determined with the\n        storage\n        building for that resource. Higher levels will allow you bigger storage just as they will allow you bigger\n        safe storage. Upgrade your storage buildings and you will be able to store more resources.")]),_c('h2',[_vm._v("8.9 Can someone steal my base?")]),_c('p',[_vm._v("No. Players can attack bases and steal resources but they can not destroy bases.")]),_c('h2',[_vm._v("8.10 Can I share my resources with other players?")]),_c('p',[_vm._v("You can transfer your resources by clicking on Actions and then on transfer. Keep in mind that those\n        resources need to be carried by units and different units have different carry capacities. To learn more,\n        read\n        Sharing resources with other players")]),_c('h2',[_vm._v("8.11 Why didn't I get any resources after winning a battle?")]),_c('p',[_vm._v("If your opponent had no resources then you had nothing to steal. Keep in mind that each battle has a small\n        time delas and a notification to your opponent. In this time, your opponent has the chance to prepare for\n        the\n        battle, get more units, relocate or spend his resources.")]),_c('h2',[_vm._v("8.12 Do I have to battle other players?")]),_c('p',[_vm._v("You do not have to attack other players if you do not want to but you will have to defend yourself if they\n        attack you, so have enough units ready. You can keep upgrading your buildings and receiving rewards based on\n        your production but keep in mind that battles are a way to get more of those resources by looting.")]),_c('h2',[_vm._v("8.13 What is Steem Engine?")]),_c('p',[_vm._v("Steem Engine is a smart contracts side-chain platform for the Steem blockchain. This means that it can\n        execute custom smart contracts based on data published to the Steem blockchain. DWD tokens are created on\n        Steem Engine. Visit it by clicking on https://steemscan.com/steem-engine. You will need to log in through\n        SteemConnect by using your steem login data.")]),_c('h2',[_vm._v("8.14 How can I buy DWD tokens on Steem Engine and transfer them to DrugWars?")]),_c('p',[_vm._v("Log in to Steem Engine platform. To find DWD, enter its name in the search area. You will see a similar\n        line\n        to the one you have in your wallet. It will look like this:")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image53.png"}})]),_c('p',[_vm._v("The line will have the information about the total DWD tokens on Steem Engine. To buy some of them, click\n        on\n        the second icon, the one with two little arrows on it. This will open the market page. You could have come\n        to\n        this step by clicking on that same icon in your wallet, next to DWD tokens. On that market page, below the\n        Market Depth graph of DWD, you will see the following:")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image52.png"}})]),_c('p',[_vm._v("This is the place where you can either SELL your tokens for steem or BUY them. We will cover the BUY part\n        in\n        this tutorial but the SELL part is very similar except the obvious difference that you are selling and not\n        buying. To BUY tokens, keep in mind that you are buying them with STEEMP so make sure you have STEEMP in\n        your\n        Steem Engine account. STEEMP is pegged to the value of STEEM and to have STEEMP you need to deposit STEEM\n        into\n        Steem Engine. You can learn how to do that by reading the FAQ on the Steem Engine platform. (In short: click\n        on your user name in the upper right corner - click on conversion history - click on deposit)")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image13.png"}})]),_c('p',[_vm._v("In the example above, a person wants to buy 2 DWD tokens. She needs to enter numbers in the first and\n        second\n        blank area. Given the information underneath, about the lowest ask of 0.079, she entered 0.08 for the price\n        of\n        the token and she entered 2 for the number of tokens she will buy. The total comes to 0.16 Steem and is\n        calculated automatically by the platform. The balance this person has is listed below and since the total in\n        this purchase is lower than the balance, the purchase will be able to go through. Keep in mind that these\n        numbers here are for this example ONLY and may not be the same based on how the market value of the token\n        changes. When you are at this stage of the process, click on the brown BUY DWD button to go to the next step\n        which is the confirmation screen that will look like this:")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image55.png"}})]),_c('p',[_vm._v("Review what you have done and if you are satisfied click on the BUY button. This will initiate a process\n        where you will see the code of the transaction. Just click on the green CONTINUE button. You will have to\n        login with steem connect:")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image79.png"}})]),_c('p',[_vm._v("After you enter your information and click on GET STARTED you will have to allow the transaction so click\n        on\n        the green APPROVE button. This will get you the number of your transaction. At the same time the window for\n        steem connect has opened, another one, in blue colour has opened too. It is normal to have those two opened\n        at\n        the same time at this part of the process. When you are done with the steem connect and get the information\n        about your code, you can close it. You will continue this process with the other window that has opened and\n        which will look like this:")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image83.png"}})]),_c('p',[_vm._v("CLICK on TRANSACTION SENT just like the explanation in that window is telling you to. This is it, you have\n        successfully purchased DWD tokens on Steem Engine and they will be visible in your Steem Engine wallet\n        shortly.")]),_c('p',[_vm._v("To send your tokens from Steem Engine to your DrugWars account, you first need to have them on Steem\n        Engine.\n        This can be done by buying them (as we have just described), getting them from other users or by having them\n        after you have withdrawn them from the game. Maybe you have changed your mind and want them back, it really\n        does not matter. As long as you have DWD tokens on Steem Engine, you can transfer them to the game. Remember\n        that line, with the info about the token, in your Steem Engine wallet? The one that looks like this:")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image32.png"}})]),_c('p',[_vm._v("There are icons at the end of the line. Click on the second one from the right, the one that looks like a\n        little arrow is coming out of the box. It will open the following:")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image103.png"}})]),_c('p',[_vm._v("The TRANSFER window is pretty simple. Enter the amount you want to send to your account and where it says\n        \"TO\", YOU MUST TYPE IN DrugWars just as it is typed in the example above. You do not have to put in the @\n        sign, it is already there, just type “drugwars” as seen in the above image. Tokens will arrive\n        at\n        your account in the DrugWars game. If you are sending to Server 1 Detroit, the memo can remain blank. If you\n        are sending to Server 2 LA, enter “s2” in the Memo field. If you do not enter s2, it will go to\n        server 1 by default. Click on the brown SEND DWD button and the transfer process will begin. It will be very\n        similar to the one where you are buying tokens because you will get the screen with the code where you need\n        to\n        click on continue:")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image118.png"}})]),_c('p',[_vm._v("You will need to log in with your steem connect information, click on \"Get started\", and approve the\n        transaction in the next step:")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image107.png"}})]),_c('p',[_vm._v("After you have logged in with steem connect and approved your transaction, that blue screen will still be\n        waiting for you, the same one that you get when you are buying tokens and looks like this:")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image83.png"}})]),_c('p',[_vm._v("Click on TRANSACTION SENT. Those two screens, the blue one and the steem connect one, open at the same\n        time,\n        do not be alarmed by this. Simply focus on steem connect and once you are done and have closed it, finish\n        with\n        the blue screen. This is it! You have successfully transferred tokens from Steem Engine to your DrugWars\n        account and you will be able to see them in the game shortly after you have completed this process.")]),_c('h2',[_vm._v("7.15 How can I withdraw my DWD tokens from the game to Steem Engine?")]),_c('p',[_vm._v("To withdraw your DWD tokens from DrugWars game to Steem Engine, you first need to click on the MARKET link\n        in\n        the left menu of DrugWars. It will open the \"Market\" part of the platform in the main screen. You will see\n        the\n        SHOP link opened automatically. To be able to withdraw your tokens, click on the EXCHANGE link which is\n        located next to SHOP.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image12.jpg"}})]),_c('p',[_vm._v("You will see the information about how many tokens you have in the game. You can withdraw up to the amount\n        you have, from 1 as the minimum to your amount as the maximum. Simply enter the number in the field and\n        click\n        on WITHDRAW. Your entered number must be an integer. You can not withdraw decimal amounts. When you have\n        clicked on the green withdraw button, a small message of confirmation will appear in the bottom left corner:\n      ")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image102.png"}})]),_c('p',[_vm._v("All you need to do now is go to your Steem Engine account and click on WALLET in the menu at the top of the\n        page. There you will see all of your tokens including DWD. Keep in mind that you will sometimes need to wait\n        a\n        short period of time for tokens to arrive. You will see a line in your wallet with the information about how\n        much tokens you have and what their price is.")]),_c('p',[_c('img',{attrs:{"src":"/img/help/image32.png"}})]),_c('p',[_vm._v("In the example above, there are 115 tokens in the wallet and those 115 are worth $0.73 with the percentage\n        telling you how much the price has gone up, if green, or gone down, if the percentage will be in red. There\n        are 4 little icons at the end of that line. If you move your mouse over them, you will see how they are\n        links\n        to information, market, send tokens, and token history. Now that you have your tokens on Steem Engine, you\n        can\n        sell them or send them to others. THAT IS IT! You have successfully withdrawn your tokens from the game to\n        Steem Engine.")]),_c('h2',[_vm._v("7.16. Why does the price of DWD tokens keep changing?")]),_c('p',[_vm._v("As with any other token, the market determines its price. You can see that all tokens on Steem Engine\n        change\n        their price not only from one day to another but from one hour to another depending on the demand.")])])])}]

